// import { debounce } from 'lodash';
import { useState, useCallback } from 'react';

import Tab from '@mui/material/Tab';
import Tabs from '@mui/material/Tabs';
import Card from '@mui/material/Card';
import Table from '@mui/material/Table';
import Stack from '@mui/material/Stack';
import Divider from '@mui/material/Divider';
import Container from '@mui/material/Container';
import TableBody from '@mui/material/TableBody';
import { alpha, useTheme } from '@mui/material/styles';
import TableContainer from '@mui/material/TableContainer';

import { paths } from 'src/routes/paths';
import { useSearchParams } from 'src/routes/hooks';

import { isAfter } from 'src/utils/format-time';

import { useTranslate } from 'src/locales';
import { useAuthContext } from 'src/auth/hooks';
import { useGetIncomePaymentControl } from 'src/api';

import Label from 'src/components/label';
import Scrollbar from 'src/components/scrollbar';
import { useSettingsContext } from 'src/components/settings';
import CustomBreadcrumbs from 'src/components/custom-breadcrumbs';
import {
  useTable,
  TableNoData,
  TableHeadCustom,
  TablePaginationCustom,
} from 'src/components/table';

import InvoiceAnalytic from '../invoice-analytic';
import InvoiceTableRow from '../invoice-table-row';
import InvoiceTableToolbar from '../invoice-table-toolbar';
import InvoiceTableFiltersResult from '../invoice-table-filters-result';

// ----------------------------------------------------------------------

const TABLE_HEAD = [
  { id: 'sequence_number', label: 'sequence' },
  { id: 'due_date', label: 'due date' },
  { id: 'type', label: 'type' },
  { id: 'insurance', label: 'insurance company' },
  { id: 'patient', label: 'patient' },
  { id: 'stakeholder', label: 'stakeholder' },
  { id: 'required_amount', label: 'required amount' },
  { id: 'balance', label: 'balance' },
  // { id: 'sent', label: 'Sent', align: 'center' },
  // { id: 'status', label: 'status' },
  { id: 'economic_movement', label: 'economic movement' },
  { id: '' },
];

const defaultFilters = {
  employee: '',
  patient: '',
  type: '',
  movement: '',
  insurance: '',
  status: 'all',
  startDate: null,
  endDate: null,
};

// ----------------------------------------------------------------------

export default function PaymentControlView() {
  const theme = useTheme();
  const settings = useSettingsContext();
  const table = useTable({ defaultOrderBy: 'createDate' });
  const { user } = useAuthContext();

  const searchParams = useSearchParams();
  const movement = searchParams.get('movement');

  const { t } = useTranslate();

  const [filters, setFilters] = useState({
    ...defaultFilters,
    movement: movement || '',
  });

  const {
    incomePaymentData,
    lengths,
    totals,
    refetch,
    unitServices,
    patients,
    stakeholders,
    insuranceComapnies,
  } = useGetIncomePaymentControl({
    unit_service:
      user?.employee?.employee_engagements?.[user.employee.selected_engagement]?.unit_service?._id,
    page: table.page || 0,
    sortBy: table.orderBy || 'created_at',
    rowsPerPage: table.rowsPerPage || 10,
    order: table.order || 'desc',
    select:
      'sequence_number created_at patient stakeholder economic_movement insurance is_it_installment due_date required_amount recieved balance status updated_at movements_type',
    populate: [
      { path: 'insurance', select: 'name_english name_arabic' },
      { path: 'stakeholder', select: 'name_english name_arabic' },
      { path: 'patient', select: 'name_english name_arabic' },
      { path: 'unit_service_patient', select: 'name_english name_arabic' },
      { path: 'economic_movement', select: 'sequence_number created_at' },
    ],
    ...filters,
  });

  const dateError = isAfter(filters.startDate, filters.endDate);

  const canReset = !!filters.startDate || !!filters.endDate;

  const notFound = (!incomePaymentData.length && canReset) || !incomePaymentData.length;

  const TABS = [
    { value: 'all', label: 'All', color: 'default', count: lengths.allLength || 0 },
    {
      value: 'paid',
      label: 'paid',
      color: 'success',
      count: lengths.paidLength || 0,
    },
    {
      value: 'pending',
      label: 'pending',
      color: 'warning',
      count: lengths.pendingLength || 0,
    },
  ];

  const handleFilters = useCallback(
    (name, value) => {
      table.onResetPage();
      setFilters((prevState) => ({
        ...prevState,
        [name]: value,
      }));
    },
    [table]
  );

  const handleResetFilters = useCallback(() => {
    setFilters(defaultFilters);
  }, []);

  // const handleEditRow = useCallback(
  //   (id) => {
  //     router.push(paths.unitservice.accounting.economicmovements.edit(id));
  //   },
  //   [router]
  // );

  // const handleViewRow = useCallback(
  //   (id) => {
  //     router.push(paths.unitservice.accounting.reciepts.info(id));
  //   },
  //   [router]
  // );

  const handleFilterStatus = useCallback(
    (event, newValue) => {
      handleFilters('status', newValue);
    },
    [handleFilters]
  );

  return (
    <Container maxWidth={settings.themeStretch ? false : 'xl'}>
      <CustomBreadcrumbs
        heading={t('invoices')}
        links={[
          {
            name: t('dashboard'),
            href: paths.dashboard.root,
          },
          {
            name: t('invoices'),
          },
        ]}
        // action={
        //   <Button
        //     component={RouterLink}
        //     href={paths.unitservice.accounting.economicmovements.add}
        //     variant="contained"
        //     startIcon={<Iconify icon="mingcute:add-line" />}
        //   >
        //     {t('new invoice')}
        //   </Button>
        // }
        sx={{
          mb: { xs: 3, md: 5 },
        }}
      />

      <Card
        sx={{
          mb: { xs: 3, md: 5 },
        }}
      >
        <Scrollbar>
          <Stack
            direction="row"
            divider={<Divider orientation="vertical" flexItem sx={{ borderStyle: 'dashed' }} />}
            sx={{ py: 2 }}
          >
            <InvoiceAnalytic
              title={t('total')}
              total={lengths.allLength}
              percent={100}
              price={totals.allTotal}
              icon="solar:bill-list-bold-duotone"
              color={theme.palette.secondary.main}
            />

            <InvoiceAnalytic
              title={t('paid')}
              total={lengths.paidLength}
              percent={(lengths.paidLength / lengths.allLength) * 100}
              price={totals.paidTotal}
              icon="solar:file-check-bold-duotone"
              color={theme.palette.success.main}
            />

            <InvoiceAnalytic
              title={t('pending')}
              total={lengths.pendingLength}
              percent={(lengths.pendingLength / lengths.allLength) * 100}
              price={totals.pendingTotal}
              icon="solar:sort-by-time-bold-duotone"
              color={theme.palette.warning.main}
            />
          </Stack>
        </Scrollbar>
      </Card>

      <Card>
        <Tabs
          value={filters.status}
          onChange={handleFilterStatus}
          sx={{
            px: 2.5,
            boxShadow: `inset 0 -2px 0 0 ${alpha(theme.palette.grey[500], 0.08)}`,
          }}
        >
          {TABS.map((tab) => (
            <Tab
              key={tab.value}
              value={tab.value}
              label={t(tab.label)}
              iconPosition="end"
              icon={
                <Label
                  variant={
                    ((tab.value === 'all' || tab.value === filters.status) && 'filled') || 'soft'
                  }
                  color={tab.color}
                >
                  {tab.count}
                </Label>
              }
            />
          ))}
        </Tabs>

        <InvoiceTableToolbar
          filters={filters}
          onFilters={handleFilters}
          //
          dateError={dateError}
          unitServices={unitServices}
          patients={patients}
          stakeholders={stakeholders}
          insuranceComapnies={insuranceComapnies}
        />

        {canReset && (
          <InvoiceTableFiltersResult
            filters={filters}
            onFilters={handleFilters}
            //
            onResetFilters={handleResetFilters}
            //
            results={incomePaymentData.length}
            sx={{ p: 2.5, pt: 0 }}
          />
        )}

        <TableContainer sx={{ position: 'relative', overflow: 'unset' }}>
          {/* <TableSelectedAction
            dense={table.dense}
            numSelected={table.selected.length}
            rowCount={lengths.length}
            onSelectAllRows={(checked) => {
              table.onSelectAllRows(
                checked,
                incomePaymentData.map((row) => row.id)
              );
            }}
            action={
              <Stack direction="row">
                <Tooltip title="Sent">
                  <IconButton color="primary">
                    <Iconify icon="iconamoon:send-fill" />
                  </IconButton>
                </Tooltip>

                <Tooltip title="Download">
                  <IconButton color="primary">
                    <Iconify icon="eva:download-outline" />
                  </IconButton>
                </Tooltip>

                <Tooltip title="Print">
                  <IconButton color="primary">
                    <Iconify icon="solar:printer-minimalistic-bold" />
                  </IconButton>
                </Tooltip>

                <Tooltip title="Delete">
                  <IconButton color="primary" onClick={confirm.onTrue}>
                    <Iconify icon="solar:trash-bin-trash-bold" />
                  </IconButton>
                </Tooltip>
              </Stack>
            }
          /> */}

          <Scrollbar>
            <Table size={table.dense ? 'small' : 'medium'} sx={{ minWidth: 800 }}>
              <TableHeadCustom
                order={table.order}
                orderBy={table.orderBy}
                headLabel={TABLE_HEAD}
                rowCount={incomePaymentData.length}
                numSelected={table.selected.length}
                onSort={table.onSort}
                // onSelectAllRows={(checked) =>
                //   table.onSelectAllRows(
                //     checked,
                //     incomePaymentData.map((row) => row.id)
                //   )
                // }
              />

              <TableBody>
                {incomePaymentData.map((row) => (
                  <InvoiceTableRow
                    key={row.id}
                    row={row}
                    selected={table.selected.includes(row.id)}
                    onSelectRow={() => table.onSelectRow(row.id)}
                    // onViewRow={() => handleViewRow(row.id)}
                    refetch={refetch}
                  />
                ))}
                <TableNoData notFound={notFound} />
              </TableBody>
            </Table>
          </Scrollbar>
        </TableContainer>

        <TablePaginationCustom
          count={lengths.length}
          page={table.page}
          rowsPerPage={table.rowsPerPage}
          onPageChange={table.onChangePage}
          onRowsPerPageChange={table.onChangeRowsPerPage}
          //
          dense={table.dense}
          onChangeDense={table.onChangeDense}
        />
      </Card>
    </Container>
  );
}
